import React, { useEffect, useState } from "react"
import './ArcSlider.css'


function ArcSlider(props) {

	const radius = props.radius || 100;
	const padding = props.padding || 0;
	const startAngle = isFinite(props.startAngle) ? props.startAngle : 120;
	const maxAngle = props.maxAngle || 190;
	const strokeWidth = isFinite(props.strokeWidth) ? props.strokeWidth : 10;
	const strokeColor = props.strokeColor || 'grey';
	const progressWidth = isFinite(props.progressWidth) ? props.progressWidth : 15;
	const progressColor = props.progressColor || 'green';
	const knobRadius = props.knobRadius || 25;
	const knobColor = props.knobColor || "red"
	const valueMin = props.valueMin || 0;
	const valueMax = props.valueMax || 100;
	const onChange = props.onChange ? props.onChange : (value) => { }

	const size = radius * 2 + knobRadius * 2 + padding * 2 + Math.max(strokeWidth, progressWidth)
	const centerX = size / 2;
	const centerY = size / 2;
	const endAngle = startAngle + maxAngle;

	const [knobCoords, setKnobCoords] = useState({ x: 0, y: 0 })
	const [knobAngle, setKnobAngle] = useState(startAngle);

	const [input, setInput] = useState({ x: 0, y: 0, pressed: false })


	useEffect(() => {
		setKnobCoords(polarToCartesian(centerX, centerY, radius, knobAngle))
	}, [centerX, centerY, size, radius, knobAngle])



	function inputDown() {
		setKnobAngle(knobAngle - 10)
		setInput({ ...input, pressed: true })
	}
	function inputUp() {
		setInput({ ...input, pressed: false })
	}
	function inputMove(x, y) {
		setInput({ ...input, x, y })
		if (input.pressed) {
			let delta = (360 - maxAngle) / 2
			let deltaAngle = startAngle - delta

			const { x: startx, y: starty } = polarToCartesian(centerX, centerY, radius, deltaAngle)
			let [mx, my] = toUnitVector(centerX, centerY, input.x, input.y)
			mx *= radius;
			my *= radius;
			mx += centerX;
			my += centerY;

			let angle = angleBetween(centerX, centerY, startx, starty, mx, my)
			angle = deltaAngle + angle
			if (angle <= startAngle) angle = startAngle
			if (angle >= endAngle) angle = endAngle
			setKnobAngle(angle)


			const { x: sx, y: sy } = polarToCartesian(centerX, centerY, radius, startAngle)
			const { x: kx, y: ky } = polarToCartesian(centerX, centerY, radius, angle)
			let ang = angleBetween(centerX, centerY, sx, sy, kx, ky)

			ang = mapRange(ang, 0, maxAngle, valueMin, valueMax)

			onChange(Math.trunc(ang))
		}
	}

	function mouseDownHandler(e) {
		e.preventDefault();
		inputDown();
	}

	function mouseUpHandler(e) {
		e.preventDefault();
		inputUp();
	}
	function mouseLeaveHandler(e) {
		e.preventDefault();
		inputUp();
	}
	function mouseMoveHandler(e) {
		e.preventDefault();
		const rect = e.currentTarget.getBoundingClientRect();
		const [mouseX, mouseY] = [e.clientX - rect.left, e.clientY - rect.top]
		inputMove(mouseX, mouseY)
	}

	function touchMoveHandler(e) {
		const rect = e.currentTarget.getBoundingClientRect();
		const [touchX, touchY] = [e.touches[0].clientX - rect.left, e.touches[0].clientY - rect.top]
		inputMove(touchX, touchY)
	}

	function touchStartHandler(e) {
		inputDown()
	}

	function touchEndHandler(e) {
		e.preventDefault();
		inputUp()
	}

	return (
		<div
			className="arc-slider"
			onMouseDown={mouseDownHandler}
			onMouseUp={mouseUpHandler}
			onMouseLeave={mouseLeaveHandler}
			onMouseMove={mouseMoveHandler}
			onTouchMove={touchMoveHandler}
			onTouchStart={touchStartHandler}
			onTouchEnd={touchEndHandler}
		>
			<svg
				width={`${size}px`}
				height={`${size}px`}
				viewBox={`0 0 ${size} ${size}`}
				style={{
					padding: `${padding}px`,
					boxSizing: "border-box",
				}}
			>
				<path
					style={{
						strokeLinecap: "round",
						strokeWidth: `${strokeWidth}px`,
						stroke: `${strokeColor}`,
						fill: "none",
					}}
					d={
						describeArc(centerX, centerY, radius, startAngle, endAngle)
					}
				/>
				<path
					style={{
						strokeLinecap: "round",
						strokeWidth: `${progressWidth}px`,
						stroke: `${progressColor}`,
						fill: "none",
					}}
					d={
						describeArc(centerX, centerY, radius, startAngle, knobAngle) // 360+
					}
				/>
				<circle
					r={knobRadius}
					cx={knobCoords.x}
					cy={knobCoords.y}
					style={{
						fill: knobColor,
						cursor: "pointer",
					}}
				/>
			</svg>
		</div>
	)
};

function polarToCartesian(centerX, centerY, radius, angleInDegrees) {
	let angleInRadians = (angleInDegrees) * Math.PI / 180.0;

	return {
		x: centerX + (radius * Math.cos(angleInRadians)),
		y: centerY + (radius * Math.sin(angleInRadians))
	};
}

function describeArc(x, y, radius, startAngle, endAngle) {

	const end = polarToCartesian(x, y, radius, startAngle);
	const start = polarToCartesian(x, y, radius, endAngle);

	const largeArcFlag = endAngle - startAngle >= 180 ? "1" : "0";

	const d = [
		"M", start.x, start.y,
		"A", radius, radius, 0, largeArcFlag, 0, end.x, end.y
	].join(" ");

	return d;
}

function angleBetween(cx, cy, ax, ay, bx, by) {
	let [x1, y1] = toUnitVector(cx, cy, ax, ay)
	let [x2, y2] = toUnitVector(cx, cy, bx, by)
	let ang = Math.atan2((x1 * y2 - y1 * x2), (x1 * x2 + y1 * y2))
	if (ang < 0) ang = Math.PI * 2 + ang
	return (ang * (180 / Math.PI))
}

function toUnitVector(x1, y1, x2, y2) {
	let dist = distanceBetwen(x1, y1, x2, y2);
	return [(x2 - x1) / dist
		, (y2 - y1) / dist];
}

function distanceBetwen(x1, y1, x2, y2) {
	const px1 = Math.min(x1, x2);
	const py1 = Math.min(y1, y2);

	const px2 = Math.max(x1, x2);
	const py2 = Math.max(y1, y2);

	return Math.sqrt((px2 - px1) * (px2 - px1) + (py2 - py1) * (py2 - py1));

}

function mapRange(value, low1, high1, low2, high2) {
	if (value < low1) value = low1;
	if (value > high1) value = high1;
	return low2 + (high2 - low2) * (value - low1) / (high1 - low1);
}


export default ArcSlider;
