import React, { useState } from "react"
import ArcSlider from './components/ArcSlider/ArcSlider';
import SimpleCard from "./components/ExampleCards/SimpleCard/SimpleCard";

function ExampleSlidersPage(props) {
	const [slider1, setSlider1] = useState(0);
	const [slider2, setSlider2] = useState(-256);
	const [slider3, setSlider3] = useState(0);
	return (
		<div className='container'>
			<div className='vc'>
				<span style={{ fontSize: '45px' }}>{slider1}</span>
				<ArcSlider onChange={(value) => { setSlider1(value) }} />
			</div>
			<div className='vc'>
				<span style={{ fontSize: '45px' }}>{slider2}</span>
				<ArcSlider
					onChange={(value) => { setSlider2(value) }}
					valueMin={-256}
					valueMax={256}
					startAngle={0}
					maxAngle={180}
					strokeColor='black'
					strokeWidth={2}
					progressColor='gold'
					progressWidth={4}
					knobRadius={15}
					knobColor='blue' />
			</div>
			<SimpleCard>
				<span style={{ fontSize: '135px' }}>{slider3}</span>
				<ArcSlider
					radius={60}
					strokeWidth={15}
					progressWidth={5}
					startAngle={170}
					maxAngle={280}
					onChange={(value) => { setSlider3(value) }}
				/>
			</SimpleCard>
		</div>
	)
};

export default ExampleSlidersPage;
